.editr {
  border: 1px solid #e4e4e4;
  width: 100%;
}

.editr--toolbar {
  background: #f6f6f6;
  border-bottom: 1px solid #e4e4e4;
  position: relative;
  display: flex;
  height: 32px;
}

.editr--toolbar a {
  display: inline-block;
  width: 8vw;
  max-width: 32px;
  height: 32px;
  color: #333;
  fill: #333;
  cursor: pointer;
  text-align: center;
  line-height: 1;
}

.editr--toolbar a:hover {
  background: rgba(0, 0, 0, 0.1);
}

.editr--toolbar a:active {
  background: rgba(0, 0, 0, 0.2);
}

.editr--toolbar a svg {
  width: 16px;
  height: 16px;
  margin: 8px auto;
}

.editr--toolbar a svg path {
  fill: inherit;
}

.editr--toolbar a.vw-btn-separator {
  width: 1px;
  margin: 0 8px;
}

.editr--toolbar a.vw-btn-separator:hover {
  background: initial;
  cursor: default;
}

.editr--toolbar a.vw-btn-separator i.vw-separator {
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  height: 100%;
  position: absolute;
  width: 1px;
}

.editr--toolbar .dashboard {
  width: 100%;
  position: absolute;
  top: 32px;
  left: 0;
  text-align: left;
  padding: 8px 16px;
  background: rgba(255, 255, 255, 0.95);
  border: 1px solid #f6f6f6;
}

.editr--content {
  min-height: 150px;
  padding: 12px 8px 16px 8px;
  line-height: 1.33;
  font-family: inherit;
  color: inherit;
  overflow-y: auto;
}

.editr--content[contenteditable="true"]:empty:before {
  content: attr(placeholder);
  color: rgba(0, 0, 0, 0.3);
  display: block;
  /* For Firefox */
}

.editr--content img {
  max-width: 100%;
}

.editr--content table {
  width: 100%;
  border-collapse: collapse;
}

.editr--content table th {
  text-align: left;
}

.editr--content table th,
.editr--content table td {
  border: 1px solid #ddd;
  padding: 2px;
}

.editr--content:focus {
  outline: 0;
}

.editr--content ul li,
.editr--content ol li {
  list-style-position: inside;
}

@media screen and (max-width: 320px) {
  .editr--toolbar a {
    margin: 0 2px;
  }

  .editr--toolbar a.vw-btn-separator {
    display: none;
  }
}

